// just so my eyes don't hurt
body {
    background-color: #00caa9;
    font-family: 'Vollkorn', serif;
}
header, footer {
    text-align: center;
    font-style: italic;
}
[class*="__inner"]{
    max-width: 960px;
    margin: 0 auto;
}
a {
    overflow: hidden;
    position: relative;
    display: inline-block;
    padding: 0 .2em;
    color: whitesmoke;
    text-decoration: none;
    border-radius: .2em;
    transition: color 300ms, box-shadow 300ms;
    &.hoverd,
    &:visited,
    &:hover {
        box-shadow: 0 1px 1px rgba(black, .3);
        color: teal;
        &:before {
            top: 0;
            background-color: aquamarine;
        }
    }
    &:before {
        content: "";
        position: absolute;
        z-index: -1;
        bottom: 0;
        top: calc(100% - 1px);
        left: 0;
        right: 0;
        background-color: rgba(aquamarine,.5);
        transition: top 200ms;
    }
}
%h0 {
    font-family: 'Signika', sans-serif;
    font-weight: bold; // allows to extend non headings consistently
    text-shadow: 0 1px 1px rgba(#000,.2);
    color: aquamarine;
}
